@media(max-width:500px){
    .productdisplay{
        flex-direction:column;
    }
    .productdisplay-left{
        gap:0px;
        flex-direction:row;
        margin: auto;
    }
    .productdisplay-img-list{
        flex-direction:column;
        gap:8px;
    }
    .productdisplay-img-list img{
        height:75px;
    }
    .productdisplay-main-img{
        height: 330px;
    }
    .productdisplay-right {
        margin: 5px;
    }
    .productdisplay-right h1{
        margin-top:15px;
        font-size: 20px;
        font-weight: 500;
    }
    .productdisplay-right-stars img{
        width:15px
    }
    .productdisplay-right-description{
        font-size: 10px;
    }
    .productdisplay-right-size h1{
        margin-top:10px;
        gap: 5px;
    }
    .productdisplay-right-sizes{
        margin: 20px 0px;
        gap: 10px;
    }
    .productdisplay-right-sizes div{
        padding: 10px 16px;
        font-size: 14px;
    }
    .productdisplay-right button{
        width:130px;
        padding:12px 0px;
        font-size:15px;
    }
    .productdisplay-right-category{
        font-size:16px;
    }
