
.new-collections{
    display:flex;
    flex-direction: column;
    align-items: center;
    gap:15px;
    margin-bottom: 100px;
    position:relative;
    top:200px;

}
.new-collections h1{
    color: #171717;
    font-size:50px;
    font-weight:600;
}

.new-collections hr{
    width: 200px;
    height: 16px;
    border-radius: 10px;
    background: #252525;
}
.collections{
    display:grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top:50px;
    gap:30px
}

@media(max-width: 1280px){
    .newsletter{
        padding: 0px 80px;
        margin-bottom: 120px;
        gap: 20px;
    }
    .newsletter h1{
        font-size: 36px;
    }
    .newsletter p{
        font-size:16px;
    }
    .newsletter div{
        width: 600px;
        height: 60px;
    }
    .newsletter div input{
        width:400px;
    }
    .newsletter div button{
        width: 160px;
        height:60px;
    }
}

@media(max-width: 1024px){
    .newsletter{
    
        margin-bottom: 60px;
        
    }
    .newsletter h1{
        font-size: 30px;
    }
    .newsletter p{
        font-size:14px;
    }
    .newsletter div{
        width: 500px;
        height: 50px;
    }
    .newsletter div input{
        width:300px;
        font-size: 14px;
    }
    .newsletter div button{
        width: 120px;
        height:50px;
        font-size:14px;
    }
}

@media(max-width: 800px){
    .newsletter{
        
        margin-bottom: 30px;
        height: 35vh;
    }
    .newsletter h1{
        font-size: 18px;
    }
    .newsletter p{
        font-size:12px;
    }
    .newsletter div{
        width: 400px;
        height: 40px;
    }
    .newsletter div input{
        width:200px;
        font-size: 13px;
    }
    .newsletter div button{
        width: 100px;
        height:40px;
        font-size: 14px;
    }
}

@media(max-width: 500px){
    .newsletter{
        height:25vh;
        width:100%;
        padding: 0;
        gap: 15px;

    }
    .newsletter h1{
        font-size: 16px;
    }
    .newsletter p{
        font-size:13px;
        max-width: 200px;
        text-align: center;
    }
    .newsletter div{
        width: 290px;
    }
    .newsletter div input{
        width:130px;
    }
    
}